import React, { Suspense, lazy } from 'react';
import { BrowserRouter, Route, Switch, Link} from 'react-router-dom';

const DatosMundiales = lazy(() => import('./DatosMundiales'));
const DatosCountry = lazy(() => import('./DatosCountry'));
const DatosRegionesPeru = lazy(() => import('./DatosRegionesPeru'));

const Navbar = () => {
  return (
    <BrowserRouter>
        
      <header>

        <nav className="navbar navbar-expand-md navbar-light fixed-top bg-light">
          <img className="Awebo" alt="" />
          <Link className="navbar-brand" to="/Covid19-Peru/">Covid19</Link>
          <button className="navbar-toggler collapsed" type="button" data-toggle="collapse" data-target="#navbarCollapse" aria-controls="navbarCollapse" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="navbar-collapse collapse" id="navbarCollapse">
            <ul className="navbar-nav mr-auto">
              <li className="nav-item">
                <Link className="nav-link" to="/Covid19-Peru/DatosMundiales">DatosMundiales</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/Covid19-Peru/DatosCountry">DatosCountry</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/Covid19-Peru/DatosRegionesPeru">DatosRegionesPeru</Link>
              </li>
            </ul>
          </div>
        </nav>

        <Suspense fallback={<div>Loading...</div>}>
          <Switch>
            <Route exact path="/Covid19-Peru/">
              <DatosMundiales />
              <DatosCountry />
              <DatosRegionesPeru />
            </Route>
            <Route exact path="/Covid19-Peru/DatosMundiales" component={DatosMundiales}/>
            <Route exact path="/Covid19-Peru/DatosCountry" component={DatosCountry}/>
            <Route exact path="/Covid19-Peru/DatosRegionesPeru" component={DatosRegionesPeru}/>
          </Switch>
        </Suspense>
        
      </header>
    </BrowserRouter>
  )
}
export default Navbar
